import * as React from "react"

import { useI18next } from "gatsby-plugin-react-i18next"
import { useTranslation } from "gatsby-plugin-react-i18next"

import Container from "react-bootstrap/Container"
import Nav from "react-bootstrap/Nav"
import Navbar from "react-bootstrap/Navbar"
import NavDropdown from "react-bootstrap/NavDropdown"
import { Button, Dropdown } from "react-bootstrap"

import Icon from "@mdi/react"
import { mdiEarth } from "@mdi/js"

import logo from "/src/assets/images/ironchip-logo-text.svg"

import "/src/assets/css/ironchip.scss"
import "/src/assets/css/components/navigation.scss"

function CollapsibleNavigation() {
  const { t } = useTranslation()
  const { language, languages, changeLanguage } = useI18next()
  return (
    <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark">
      <Container>
        <Navbar.Brand href="/">
          <img className="m-0" src={logo} alt="Ironchip logo" height={30} />
        </Navbar.Brand>

        <Navbar.Collapse
          id="responsive-navbar-nav"
          className="order-2 order-lg-1"
        >
          <Nav className="ms-auto">
            <NavDropdown title={t("products")} id="collasible-nav-dropdown">
              <NavDropdown.Item href="/products/lbauth">
                {t("lbauth")}
              </NavDropdown.Item>
              <NavDropdown.Item href="#action/3.2">
                {t("lbfraud")}
              </NavDropdown.Item>
            </NavDropdown>
            <NavDropdown title={t("resources")} id="collasible-nav-dropdown">
              <NavDropdown.Item href="/products/lbauth">
                {t("lbauth")}
              </NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item href="#action/3.4">
                Separated link
              </NavDropdown.Item>
            </NavDropdown>
            <Nav.Link href="#pricing">{t("about-us")}</Nav.Link>
            <Nav.Link href="#pricing">{t("contact")}</Nav.Link>
          </Nav>
        </Navbar.Collapse>

        <div className="d-flex order-1 order-lg-2 align-items-center">
          <Dropdown className="mr-2">
            <Dropdown.Toggle
              variant="success"
              id="dropdown-basic"
              className="d-flex align-items-center color-white"
            >
              <Icon className="mx-1" path={mdiEarth} title="Globe" size={1} />
            </Dropdown.Toggle>

            <Dropdown.Menu>
              {languages.map((lng, index) => (
                <Dropdown.Item
                  key={index}
                  href="#"
                  onClick={e => {
                    e.preventDefault()
                    changeLanguage(lng)
                  }}
                  disabled={language === lng}
                >
                  {t(lng)}
                </Dropdown.Item>
              ))}
            </Dropdown.Menu>
          </Dropdown>
          <Button
            variant="outline-secondary"
            href="https://app.ironchip.com/"
            className="mr-3"
          >
            {t("login")}
          </Button>
          <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        </div>
      </Container>
    </Navbar>
  )
}

export default CollapsibleNavigation
